import { elizaLogger, IAgentRuntime } from "@ai16z/eliza";
import {
    ChatMessage,
    ChatRoom,
    EchoChamberConfig,
    ModelInfo,
    ListRoomsResponse,
    RoomHistoryResponse,
    MessageResponse,
} from "./types";

/**
 * Maximum number of retries for API requests
 */
const MAX_RETRIES = 3;

/**
 * Delay between retries in milliseconds
 */
const RETRY_DELAY = 5000;

/**
 * EchoChamberClient handles communication between ELIZA and the EchoChambers platform.
 * Primary responsibilities:
 * 1. API communication
 * 2. Room management
 * 3. Message sending/receiving
 *
 * Error Handling:
 * - Automatic retry for failed API requests
 * - Graceful degradation on connection issues
 * - Detailed error logging
 */
export class EchoChamberClient {
    private runtime: IAgentRuntime;
    private config: EchoChamberConfig;
    private apiUrl: string;
    private modelInfo: ModelInfo;
    private pollInterval: NodeJS.Timeout | null = null;
    private watchedRoom: string | null = null;
    private reconnectAttempts: number = 0;
    private readonly maxReconnectAttempts: number = 5;

    constructor(runtime: IAgentRuntime, config: EchoChamberConfig) {
        this.runtime = runtime;
        this.config = config;
        this.apiUrl = `${config.apiUrl}/api/rooms`;
        this.modelInfo = {
            username: config.username || `agent-${runtime.agentId}`,
            model: config.model || runtime.modelProvider,
        };
    }

    /**
     * Gets the username configured for this client
     */
    public getUsername(): string {
        return this.modelInfo.username;
    }

    /**
     * Gets the model info for this client
     */
    public getModelInfo(): ModelInfo {
        return { ...this.modelInfo };
    }

    /**
     * Gets the current configuration
     */
    public getConfig(): EchoChamberConfig {
        return { ...this.config };
    }

    private getAuthHeaders(): { [key: string]: string } {
        return {
            "Content-Type": "application/json",
            "x-api-key": this.config.apiKey,
        };
    }

    /**
     * Sets a specific room to watch and process messages from
     * @param roomId ID of the room to watch
     * @throws {Error} If room not found
     */
    public async setWatchedRoom(roomId: string): Promise<void> {
        try {
            // Verify room exists
            const rooms = await this.listRooms();
            const room = rooms.find((r) => r.id === roomId);

            if (!room) {
                throw new Error(`Room ${roomId} not found`);
            }

            // Leave current watched room if any
            if (this.watchedRoom) {
                await this.leaveRoom(this.watchedRoom);
            }

            // Set new watched room
            this.watchedRoom = roomId;
            await this.joinRoom(roomId);

            elizaLogger.success(`Now watching room: ${room.name}`);
        } catch (error) {
            elizaLogger.error("Error setting watched room:", error);
            throw error;
        }
    }

    /**
     * Gets the currently watched room ID
     * @returns The watched room ID or null if no room is being watched
     */
    public getWatchedRoom(): string | null {
        return this.watchedRoom;
    }

    /**
     * Retries an operation with exponential backoff
     * @param operation The async operation to retry
     * @param retries Maximum number of retry attempts
     * @throws {Error} If all retries fail
     */
    private async retryOperation<T>(
        operation: () => Promise<T>,
        retries: number = MAX_RETRIES
    ): Promise<T> {
        for (let i = 0; i < retries; i++) {
            try {
                return await operation();
            } catch (error) {
                if (i === retries - 1) throw error;
                const delay = RETRY_DELAY * Math.pow(2, i);
                elizaLogger.warn(`Retrying operation in ${delay}ms...`);
                await new Promise((resolve) => setTimeout(resolve, delay));
            }
        }
        throw new Error("Max retries exceeded");
    }

    /**
     * Handles reconnection attempts when polling fails
     */
    private async handleReconnection(): Promise<void> {
        this.reconnectAttempts++;
        if (this.reconnectAttempts <= this.maxReconnectAttempts) {
            elizaLogger.warn(
                `Attempting to reconnect (${this.reconnectAttempts}/${this.maxReconnectAttempts})...`
            );
            await new Promise((resolve) => setTimeout(resolve, RETRY_DELAY));
        } else {
            elizaLogger.error(
                "Max reconnection attempts reached, stopping client"
            );
            await this.stop();
        }
    }

    /**
     * Initializes the EchoChamber client
     * 1. Verifies API connection
     * 2. Joins default room if specified
     *
     * @throws {Error} If API connection fails after max retries
     */
    public async start(): Promise<void> {
        elizaLogger.log("🚀 Starting EchoChamber client...");
        try {
            // Verify connection by listing rooms
            await this.retryOperation(() => this.listRooms());
            elizaLogger.success(
                `✅ EchoChamber client successfully started for ${this.modelInfo.username}`
            );

            // Join default room if specified and no specific room is being watched
            if (this.config.defaultRoom && !this.watchedRoom) {
                await this.setWatchedRoom(this.config.defaultRoom);
            }
        } catch (error) {
            elizaLogger.error("❌ Failed to start EchoChamber client:", error);
            throw error;
        }
    }

    /**
     * Stops the client and cleans up resources
     */
    public async stop(): Promise<void> {
        if (this.pollInterval) {
            clearInterval(this.pollInterval);
            this.pollInterval = null;
        }

        // Leave watched room if any
        if (this.watchedRoom) {
            try {
                await this.leaveRoom(this.watchedRoom);
                this.watchedRoom = null;
            } catch (error) {
                elizaLogger.error(
                    `Error leaving room ${this.watchedRoom}:`,
                    error
                );
            }
        }

        elizaLogger.log("Stopping EchoChamber client...");
    }

    /**
     * Lists available rooms, optionally filtered by tags
     * @param tags Optional array of tags to filter rooms
     * @returns Array of ChatRoom objects
     * @throws {Error} If API request fails
     */
    public async listRooms(tags?: string[]): Promise<ChatRoom[]> {
        try {
            const url = new URL(this.apiUrl);
            if (tags?.length) {
                url.searchParams.append("tags", tags.join(","));
            }

            const response = await fetch(url.toString());
            if (!response.ok) {
                throw new Error(`Failed to list rooms: ${response.statusText}`);
            }

            const data = (await response.json()) as ListRoomsResponse;
            return data.rooms;
        } catch (error) {
            elizaLogger.error("Error listing rooms:", error);
            throw error;
        }
    }

    /**
     * Retrieves message history for a specific room
     * @param roomId ID of the room to get history for
     * @returns Array of ChatMessage objects
     * @throws {Error} If API request fails
     */
    public async getRoomHistory(roomId: string): Promise<ChatMessage[]> {
        return this.retryOperation(async () => {
            const response = await fetch(`${this.apiUrl}/${roomId}/history`);
            if (!response.ok) {
                throw new Error(
                    `Failed to get room history: ${response.statusText}`
                );
            }

            const data = (await response.json()) as RoomHistoryResponse;
            return data.messages;
        });
    }

    /**
     * Sends a message to a specific room
     * @param roomId ID of the room to send message to
     * @param content Message content
     * @returns The sent ChatMessage
     * @throws {Error} If API request fails
     */
    public async sendMessage(
        roomId: string,
        content: string
    ): Promise<ChatMessage> {
        return this.retryOperation(async () => {
            const response = await fetch(`${this.apiUrl}/${roomId}/message`, {
                method: "POST",
                headers: this.getAuthHeaders(),
                body: JSON.stringify({
                    content,
                    sender: this.modelInfo,
                }),
            });

            if (!response.ok) {
                throw new Error(
                    `Failed to send message: ${response.statusText}`
                );
            }

            const data = (await response.json()) as MessageResponse;
            return data.message;
        });
    }

    /**
     * Joins a room to begin receiving messages
     * @param roomId ID of the room to join
     * @throws {Error} If room not found or join fails
     */
    private async joinRoom(roomId: string): Promise<void> {
        try {
            const rooms = await this.listRooms();
            const room = rooms.find((r) => r.id === roomId);

            if (!room) {
                throw new Error(`Room ${roomId} not found`);
            }

            // Join room using the join endpoint
            const response = await fetch(`${this.apiUrl}/${roomId}/join`, {
                method: "POST",
                headers: this.getAuthHeaders(),
                body: JSON.stringify(this.modelInfo),
            });

            if (!response.ok) {
                throw new Error(`Failed to join room: ${response.statusText}`);
            }

            elizaLogger.success(`Joined room: ${room.name}`);
        } catch (error) {
            elizaLogger.error("Error joining room:", error);
            throw error;
        }
    }

    /**
     * Leaves a room and stops receiving messages
     * @param roomId ID of the room to leave
     * @throws {Error} If leave request fails
     */
    private async leaveRoom(roomId: string): Promise<void> {
        try {
            // Leave room using the leave endpoint
            const response = await fetch(`${this.apiUrl}/${roomId}/leave`, {
                method: "POST",
                headers: this.getAuthHeaders(),
                body: JSON.stringify(this.modelInfo),
            });

            if (!response.ok) {
                throw new Error(`Failed to leave room: ${response.statusText}`);
            }

            elizaLogger.success(`Left room: ${roomId}`);
        } catch (error) {
            elizaLogger.error("Error leaving room:", error);
            throw error;
        }
    }
}
